import request from '@/utils/request'

// 
export function getUserProfile () {
  return request({
    url: 'user'
  })
}

export function getUserInfo () {
  return request({
    url: 'user/profile'
  })
}

// 
export function editUserInfo (user) {
  return request({
    url: 'user/profile',
    method: 'PATCH',
    data: user
  })
}

// 
export function editUserPhoto (formData) {
  return request({
    url: 'user/photo',
    method: 'PATCH',
    data: formData
  })
}
